import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  // 定义状态
  state: {
    author: 'Ye ChengYu'
  },
  mutations: {
  	newAuthor: function(state,newVal){
  		state.author = newVal;
  	}
  }
})

export default store


