<template>
  <div>
    <ul class="article-wrap">
      <li v-for="(item,index) in articles" class="article-item">
        <h3 class="article-item-title">
          <router-link :to="'/article/'+item.id" tag="span">{{ item.title }}</router-link>
        </h3>
        <p>{{ item.content }}</p>
        <div class="article-date">{{ item.date }}</div>
      </li>
    </ul>
  </div>
</template>

<script type="text/javascript">
  import headerDiv from './Header.vue'
  import footerDiv from './Footer'
  import simpleDiv from './Simple01'
  export default {
  name: 'App',
  data(){
    return {
      logoMsg: '是假小发不是桂',
      userName: '',
      inp: '',
      articles: [{
        id: 0,
        title: '第一篇',
        content: "第一篇的内容第一篇的内容第一篇的内容第一篇的内容第一篇的内容第一篇的内容第一篇的内容第一篇的内容",
        date: "20181107"
      },{
        id: 0,
        title: '第二篇',
        content: "第一篇的内容",
        date: "20181107"
      },{
        id: 0,
        title: '第三篇',
        content: "第一篇的内容",
        date: "20181107"
      },{
        id: 0,
        title: '第四篇',
        content: "第一篇的内容",
        date: "20181107"
      },{
        id: 0,
        title: '第五篇',
        content: "第一篇的内容",
        date: "20181107"
      }]
    }
  },
  components: {
    headerDiv,footerDiv,simpleDiv
  },
  methods: {
    getUser: function(msg){
      this.userName = msg;
    },
    setAuthor: function(){
      //this.$store.state.author = this.inp
      this.$store.commit('newAuthor',this.inp)
    }
  }
}
</script>

<style type="text/css">
  ul {
    list-style: none;
    overflow: hidden;

  }
  .article-wrap {
    padding: 0 168px;
  }
  .article-item-title {

    margin-bottom: 10px;
  }
  .article-item-title span {
    background: linear-gradient(to bottom, transparent 93%, #42b983 0) center center/0% 75% no-repeat;
    transition: all .3s ease;
    padding: 4px 0;
    font-size: 1.6em;
    color: #34495e;
    cursor: pointer;
  }
  .article-wrap p {
    margin-bottom: 10px;
    max-width: 520px;
  }
  .article-date {
    color: #7f8c8d;
    font-size: 12px;
  }
  .article-item-title span:hover {
    background-size: 100% 100%;
  }
</style>


