<template>
  <header>
    <div id="logo"><img src="../assets/cat.jpg">{{logo}}</div>
    <ul>
      <li v-for="nav in navs">
        <router-link tag="span" :to="nav.ulr">{{nav.li}}</router-link>
      </li>
    </ul>
  </header>
</template>

<script type="text/javascript">
  export default{
    name: 'headerDiv',
    data(){
      return {
        navs: [
          {li: "主页",ulr:'/'},
          {li: "留言板",ulr:'/liuyan'},
          {li: "关于",ulr:'/about'},
        ]
      }
    },
    props: ['logo']
  }
</script>

<style type="text/css" scoped>
  ul {
    list-style: none;
    overflow: hidden;

  }
  ul li {
    float: left;
    margin-right: 20px;
    height: 60px;
    line-height: 60px;
  }
  header {
    overflow: hidden;
    padding: 0px 20px;
    color: #42b983;
    box-shadow: 0 0px 3px #333;
    position: fixed;
    top: 0;
    width: 100%;
    box-sizing: border-box;
    background: white;
  }
  header ul {
    float: right;
  }
  header #logo{
    float: left;
    font-weight: bold;
    height: 60px;
    line-height: 60px;
    position: relative;
    padding-left: 70px;
  }
  header #logo img {
    width: 60px;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
  }
  header #logo img:hover {
    animation: tada 1s;
    box-shadow: 0 0 12px 0 green;
  }
  header a {
    text-decoration: none;
  }
  @keyframes tada {
    0% {
      transform: scaleX(1);
    }
    10%,20% {
      transform: scale3d(.9,.9,.9) rotate(-5deg);
    }
    30%, 50%, 70%, 90% {
      transform: scale3d(1.1,1.1,1.1) rotate(5deg);
    }
    40%, 60%, 80%{
      transform: scale3d(1.1,1.1,1.1) rotate(-5deg);
    }
    100%{
      transform: scaleX(1);
    }
  }
  li span {
    cursor: pointer;
    color: #34495e;
    transition: 1s;
  }
  li span:hover {
    color: #42b983;
  }
</style>


