      userName: '',
      inp: '',
      navs: [{
        text: 'a',
      },{
        text: 'b',
      },{
        text: 'c',
      }],
      todos: [{
        label: 'a',
        children: [{
          label: 'a-1',
          children: [{
            label: 'a-1-1',
            children: [{
            label: 'a-1-1-1'
          }]
          }]
        }]
      },{
        label: 'b',
        children: [{
          label: 'b-1'
        }]
      }]
    }
