  components: {
    headerDiv,footerDiv,simpleDiv
  },
  methods: {
    getUser: function(msg){
      this.userName = msg;
    },
    setAuthor: function(){
      //this.$store.state.author = this.inp
      this.$store.commit('newAuthor',this.inp)
    }
  }
}
</script>

