<template>
  <li>{{todo.label}}
    <ul v-if="todo.children" style="padding-left: 20px;">
      <simple-div v-for="(item,index) in todo.children" :key="index" :todo="item"></simple-div>
    </ul>
  </li>
</template>

<script type="text/javascript">
  
  export default{
    name: 'simpleDiv',
    data: function(){
      return {

      }
    },
    props: ['todo']
  }
</script>
<style type="text/css">
  ul {
    list-style: none;
  }
</style>


